
form {
    display: flex;
    flex-direction: column;
    align-items: center;

    #Query-button {
        width: 200px;
        font-size: 1.58rem;
        background-color: #d91821;
        border-radius: 7px;
        border: none;
        padding: 10px;
        // color: white;
        margin: 10px;
    }

    label {
        color: #FFFFFF;
        width: 60%;

        input[type=text] {
            width: 100%;
            font-size: 20px;
            padding: 10px;
            border-radius: 7px;
            border: 0;
            margin: 2% 0 2%;
            display: inherit;
        }
    }
}


